import time from "../../commen/time.js"
export default {
	props: {
		item: Object
	},
	data() {
		return {
		}
	},
	methods: {
		handleAddAttention() {
			uni.$emit('handleAddAttention',{
				type:"addAttention",
				userId:this.item.user_id
			})
			this.$emit('handleAddAttention')
			uni.showToast({
				title: "关注成功"
			})
		},
		//图片点击事件
		handleImgItemClick(index) {
			const imgArr = []
			this.item.images.forEach(v => {
				imgArr.push(v.url)
			})
			uni.previewImage({
				urls: imgArr,
				current: index
			})
		},
		handleUserSpace(){
			console.log(this.item)
			uni.navigateTo({
				url:"/pages/mine/mine-user-space/mine-user-space?userId=" + this.item.user.id
			})
		}
	},
	computed: {
		formatTime(){
			return time.gettime.gettime(this.item.create_time)
		}
	}
}
